import { connect } from 'react-redux';
import * as perf from 'src/utils/perf';
import * as reporter from 'src/utils/reporter';
import * as selectors from '../reducers/selectors';
import ChatContent from '../components/ChatSection/ChatContent/ChatContent';
import * as actions from '../actions/chat';
import { getSecureFormContent } from '../services/secureForm';
import { isChattingWithAgent } from '../utils/common';
import * as audioVideoChatStatus from '../constants/enums/visitorAudioVideoChatStatus';
import * as webAction from '../actions/web';
import P2PChat from '../services/p2pChat';
import { getWebrtcSignalingServiceUrl } from '../reducers/selectors/urlsManager';

const getIsContactOrUserOpen = (state) => {
  if (state.config.settings.siteInfo) {
    return (state.config.settings.siteInfo.ifContactOpened &&
      state.config.settings.siteInfo.ifUserOpened);
  }
  return false;
};

function getAVStatusForUI(state, visitor, currentAgentId) {
  if (!selectors.isVisitorExists(state, visitor.get('id'))) {
    return audioVideoChatStatus.notStart;
  }

  if (!selectors.checkIfAgentInVisitorActiveIds(
    { chatActiveAgents: visitor.get('chatActiveAgents') },
    currentAgentId
  )) {
    return audioVideoChatStatus.notStart;
  }

  const avStatus = visitor.get('audioVideoChatStatus');
  if (avStatus === audioVideoChatStatus.agentRequestAudioChat
    || avStatus === audioVideoChatStatus.agentRequestVideoChat
    || avStatus === audioVideoChatStatus.audioChatting
    || avStatus === audioVideoChatStatus.videoChatting) {
    const avChatAgentId = visitor.get('audioVideoChatAgentId');
    if (avChatAgentId === currentAgentId) {
      return avStatus;
    }

    return audioVideoChatStatus.notStart;
  }

  if (avStatus === audioVideoChatStatus.visitorRequestAudioChat
    || avStatus === audioVideoChatStatus.visitorRequestVideoChat) {
    const activeAgents = visitor.get('chatActiveAgents');
    // 当聊天里有多个agent时，当前agent如果正在语音视频，不显示访客请求语音视频界面
    if (activeAgents && activeAgents.length > 1) {
      if (selectors.getIfAgentAudioVideoChatting(state, currentAgentId)) {
        return audioVideoChatStatus.notStart;
      }
    }
  }

  return avStatus;
}

const mapStateToProps = (state) => {
  const chatId = selectors.getSelectedChatId(state);
  if (!chatId) return {};
  perf.markBegin('ChatContent: mapStateToProps');
  const chat = selectors.getChat(state, chatId);
  if (!chat) return {};
  let g2mStartUrl = chat.get('g2mStartUrl');
  const visitorId = chat.get('visitorId');
  const visitor = selectors.getVisitorByChatId(state, chatId);
  const chatGuid = visitor.get('chatGuid');
  let ifShowGotoMeetingNotify = chat.get('ifShowGotoMeetingNotify');
  const secureFormServer = selectors.getSecureFormServer(state);
  const siteId = selectors.getSiteId(state);
  const agentSession = selectors.getCurrentAgentSession(state);
  const timezone = selectors.getTimezone(state);
  const timezoneOffset = selectors.getTimezoneOffset(state);
  const textDirectionIfRTL = selectors.textDirectionIfRTL(state);
  const errorMessageForChat = selectors.getErrorMessageForChat(state, chat && chat.get('id'));
  const ifShowNotify = chat.get('ifShowNotify');
  const audioVideoChatWidth = chat.get('audioVideoChatWidth');
  const audioVideoChatStartTime =
    selectors.serverDateToLocal(state, visitor.get('audioVideoChatStartTime'));
  const currentAgentId = selectors.getCurrentAgentId(state);
  const avStatus = getAVStatusForUI(state, visitor, currentAgentId);
  if (!isChattingWithAgent(visitor, currentAgentId)) {
    g2mStartUrl = null;
    ifShowGotoMeetingNotify = false;
  }

  const ret = ({
    id: chat.get('id'),
    g2mStartUrl,
    siteId,
    visitorId,
    chatGuid,
    agentSession,
    selectedFontSize: selectors.getFontSize(state),
    messages: selectors.getChatMessages(state, selectors.getSelectedChatId(state)),
    typingMessage: chat.get('typingMessage'),
    isContactOrUserOpen: getIsContactOrUserOpen(state),
    chatTo: visitor.get('latestName'),
    visitorAvatar: visitor.get('avatar'),
    identityType: visitor.get('identityType'),
    secureFormServer,
    timezone,
    timezoneOffset,
    textDirectionIfRTL,
    errorMessageForChat,
    scrollTop: chat.get('scrollTop'),
    ifShowNotify,
    audioVideoChatWidth,
    audioVideoChatStatus: avStatus,
    webrtcSignalingServiceUrl: getWebrtcSignalingServiceUrl(state),
    audioVideoChatStartTime,
    p2pChat: chat.get('p2pChat'),
    ifShowGotoMeetingNotify,
  });
  perf.markEnd('ChatContent: mapStateToProps');
  return ret;
};

const mergeProps = (stateProps, dispatchProps) => {
  const { dispatch } = dispatchProps;

  const dispatchObject = {

    getSecureFormContent(token, rsaKey) {
      const config = {
        siteId: stateProps.siteId,
        visitorId: stateProps.visitorId,
        accessToken: token,
        authToken: stateProps.agentSession,
      };
      getSecureFormContent(stateProps.secureFormServer, config, rsaKey)
        .then((content) => {
          dispatch(actions.onGetSecureFormContent(stateProps.id, token, content));
        })
        .catch((error) => {
          reporter.error(error, 'ChatContent', 67, 0);
        });
    },
    onScroll(id, scrollTop) {
      dispatch(actions.updateScrollTop(id, scrollTop));
    },
    hideErrorMessage() {
      dispatch(actions.setErrorMessageForChat(stateProps.id, ''));
    },
    clearG2mStartUrl() {
      dispatch(actions.clearG2mStartUrl(stateProps.id));
    },
    updateIfShowNotify(ifShowNotify) {
      dispatch(actions.updateIfShowNotify(stateProps.id, ifShowNotify));
    },
    onAudioAndVideoStopResize(info) {
      dispatch(actions.audioAndVideoWidthChanged(stateProps.id, info));
    },
    onAudioVideoChatAccept() {
      const status = stateProps.audioVideoChatStatus;
      const visitorId = stateProps.visitorId;
      if (status === audioVideoChatStatus.visitorRequestAudioChat
        || status === audioVideoChatStatus.visitorRequestVideoChat) {
        const p2pChat = new P2PChat({
          url: stateProps.webrtcSignalingServiceUrl,
          localVideo: document.createElement('VIDEO'),
          remoteVideo: document.createElement('VIDEO'),
          chat: stateProps.chatGuid,
        });
        p2pChat
          .requirePermission(status === audioVideoChatStatus.visitorRequestVideoChat)
          .then(() => {
            dispatch(actions.setP2PChat(stateProps.id, p2pChat));
            if (status === audioVideoChatStatus.visitorRequestAudioChat) {
              dispatch(webAction.onAcceptAudioChat(visitorId));
            } else {
              dispatch(webAction.onAcceptVideoChat(visitorId));
            }
          });
      }
    },
    onAudioVideoChatRefuse() {
      const status = stateProps.audioVideoChatStatus;
      const visitorId = stateProps.visitorId;
      console.log('onAudioVideoChatRefuse:', status);
      if (status === audioVideoChatStatus.visitorRequestAudioChat) {
        dispatch(webAction.onRefuseAudioChat(visitorId));
      } else if (status === audioVideoChatStatus.visitorRequestVideoChat) {
        dispatch(webAction.onRefuseVideoChat(visitorId));
      } else if (status === audioVideoChatStatus.agentRequestAudioChat) {
        dispatch(webAction.onCancelAudioChatRequest(visitorId));
      } else if (status === audioVideoChatStatus.agentRequestVideoChat) {
        dispatch(webAction.onCancelVideoChatRequest(visitorId));
      } else if (status === audioVideoChatStatus.audioChatting) {
        dispatch(webAction.onStopAudioChat(visitorId));
      } else if (status === audioVideoChatStatus.videoChatting) {
        dispatch(webAction.onStopVideoChat(visitorId));
      }
    },
  };
  return {
    ...stateProps,
    ...dispatchObject,
  };
};

export default connect(mapStateToProps, null, mergeProps)(ChatContent);



