import React from 'react';
import ReactTooltip from 'react-tooltip';
import classnames from 'classnames';
import CommonStyle from '../components/CommonResources/common.css';
import ResizeContainer from '../components/Resizer/ResizeContainer';
import ChatList from './ChatList';
import ChatHeader from './ChatHeader';
import ChatContent from './ChatContent';
import ChatAction from './ChatAction';
import TextEditor from './SplitterChatTextEditor';
import NoChat from './NoChat';
import DefaultStyle from './Default.css';
import SplitterVisitorDetailsTab from './SplitterVisitorDetailsTab';

const SplitterChat = ({
  selectedChatId,
  chatItemWidth,
  onChatItemStopResize,
  chatDetailWidth,
  onChatDetailStopResize,
}) => (
  <div className={classnames(CommonStyle.layoutContent, CommonStyle.chatsbackground)} id="chats">
    {
      selectedChatId ? (
        <ResizeContainer
          resizerPosition="left"
          width={chatItemWidth}
          left_minWidth={240}
          left_maxWidth={350}
          right_minWidth={640}
          onStopResize={onChatItemStopResize}
        >
          <ChatList />
          <div className={CommonStyle.chatwindowContainer}>
            <ResizeContainer
              resizerPosition="right"
              width={chatDetailWidth}
              right_minWidth={265}
              right_maxWidth={500}
              left_minWidth={525}
              onStopResize={onChatDetailStopResize}
              rightContainerStyle={CommonStyle.mychatrightresize}
            >
              <div>
                <ChatHeader />
                <ChatContent />
                <ChatAction />
                <div
                  style={{
                    position: 'absolute',
                    width: '100%',
                    height: 175,
                    bottom: 20,
                    minHeight: 40,
                  }}
                >
                  <TextEditor />
                </div>
              </div>
              <SplitterVisitorDetailsTab />
            </ResizeContainer>
            <ReactTooltip
              id="chatContainer"
              multiline
              effect="solid"
              place="bottom"
              class={DefaultStyle.tooltip}
            />
          </div>
        </ResizeContainer>
      ) :
      (<NoChat />)
    }
  </div>
);

SplitterChat.propTypes = {
  selectedChatId: React.PropTypes.number,
  chatItemWidth: React.PropTypes.number.isRequired,
  onChatItemStopResize: React.PropTypes.func.isRequired,
  chatDetailWidth: React.PropTypes.number.isRequired,
  onChatDetailStopResize: React.PropTypes.func.isRequired,
};

export default SplitterChat;



