import { connect } from 'react-redux';
import * as _ from 'lodash';
import memoize from 'lru-memoize';
import ChatAction from 'src/components/ChatSection/ChatAction/ChatAction';
import * as perf from 'src/utils/perf';
import * as helper from 'src/utils/common';
import { toHHMMSS, format } from 'src/utils/time';
import * as messageType from 'src/constants/enums/messageType';
import * as visitorStatus from 'src/constants/enums/visitorStatus';
import * as permissionType from 'src/constants/enums/liveChatPermission';
import config from 'config.json';
import * as selectors from '../reducers/selectors';
import * as actions from '../actions/chat';
import * as uiActions from '../actions/ui';
import * as agentAction from '../actions/agent';
import * as webAction from '../actions/web';
import { turnOnAutoTranslate } from '../constants/enums/chatAction';
import { requestSecureForm } from '../services/secureForm';
import * as common from './Business/common';
import P2PChat from '../services/p2pChat';
import { getWebrtcSignalingServiceUrl } from '../reducers/selectors/urlsManager';
import * as audioVideoChatStatus from '../constants/enums/visitorAudioVideoChatStatus';

const getInitialTranscriptBody = memoize(1)(
  (visitor, chatId, chatMessages, chatInfo, dateTimeFormat, ifDepartmentEnabled) => {
    let chatInfoMsg = '';
    let chatInfoDetail = '';
    let chatContent = '<tr><td></td></tr>';
    let chatAttachments = '';
    let customVariables = '';
    let customFields = '';
    if (chatInfo) {
      chatMessages.forEach((message) => {
        _.each(message, (item) => {
          if (item.type === messageType.system) {
            chatInfoMsg += `${item.message}<br/>`;
          }
          if ((item.type === messageType.agent || item.type === messageType.visitor)
            && item.message) {
            chatInfoMsg += `[${toHHMMSS(item.time)}] ${item.sender}: ${item.message}<br/>`;
          }
        });
      });
      _.each(chatInfo.customVariables, (item) => {
        customVariables += `${item.name}: ${item.value}<br/>`;
      });
      _.each(chatInfo.preChatFields, (item) => {
        customFields += `${item.name}: ${item.value.split(config.splitter).join(',')}<br/>`;
      });

      chatInfoDetail = [
        `<tr><td>Id: ${chatId}</td></tr>`,
        `<tr><td>Name: ${chatInfo.name}</td></tr>`,
        `<tr><td>Email: ${chatInfo.email}</td></tr>`,
        `<tr><td>Department: ${ifDepartmentEnabled ? chatInfo.department : ''}</td></tr>`,
        `<tr><td>Agent: ${chatInfo.agentNames}</td></tr>`,
        `<tr><td>Chat Start Time: ${format(chatInfo.chatStartTime, dateTimeFormat)}</td></tr>`,
        `<tr><td>End Time: ${format(chatInfo.chatEndTime, dateTimeFormat)}</td></tr>`,
        `<tr><td>Product/Service: ${chatInfo.service}</td></tr>`,
        `<tr><td>Phone: ${chatInfo.phone}</td></tr>`,
        `<tr><td>Company: ${chatInfo.company}</td></tr>`,
        `<tr><td>Source: ${chatInfo.source}</td></tr>`,
        `<tr><td>Auto Invitation: ${chatInfo.autoInvitation}</td></tr>`,
        `<tr><td>Request Page: ${chatInfo.requestingPageUrl}</td></tr>`,
        `<tr><td>Custom Variables:</td></tr><tr><td>${customVariables.substr(0, customVariables.length - 5)}&nbsp;</td></tr>`,
        `<tr><td>Custom Fields:</td></tr><tr><td style="color:#666;">${customFields}&nbsp;</td></tr>`,
      ].join('');

      chatContent = [
        '<tr><td style="color:#329fd9;"><strong>Chat Info</strong></td></tr>',
        `<tr><td style="color:#666;">${chatInfoMsg}</td><tr><td>&nbsp;</td></tr></tr>${chatInfoDetail}`,
      ].join('');
      chatAttachments = chatInfo.attachments.join(', ');
    }

    return {
      content: `<table cellspacing="0" cellpadding="0" style="font-family: SourceSans Pro Bold,Arial;">${chatContent}</table>`,
      attachments: chatAttachments,
    };
  });

const getTranscriptVisitorInfo = memoize(1)((visitor, dateTimeFormat) => [
  '<table cellspacing="0" cellpadding="0"><tr><td>&nbsp;</td></tr><tr><td style="color:#329fd9"><b>Visitor Info</b></td></tr>',
  `<tr><td>Visitor Id: ${visitor.get('id')}</td></tr>`,
  `<tr><td>Latest Name: ${visitor.get('latestName')}</td></tr>`,
  `<tr><td>Latest Email: ${visitor.get('latestEmail')}</td></tr>`,
  `<tr><td>First Visit Time: ${format(visitor.get('firstVisitTime'), dateTimeFormat)}</td></tr>`,
  `<tr><td>Chat Times: ${visitor.get('chatTimes')}</td></tr>`,
  `<tr><td>Visit Times: ${visitor.get('visitorTimes')}<br/></td></tr></table>`,
].join(''));

const getTranscriptNavigation = memoize(1)((sessionInfo, dateTimeFormat) => (
  sessionInfo ? [
    '<table cellspacing="0" cellpadding="0"><tr><td>&nbsp;</td></tr><tr><td style="color:#329fd9"><b>Navigation</b></td></tr>',
    `<tr><td>Start Time: ${format(sessionInfo.sessionStartTime, dateTimeFormat)}</td></tr>`,
    `<tr><td>Referrer: ${sessionInfo.referrerUrl}</td></tr>`,
    `<tr><td>Search Engine: ${sessionInfo.searchEngine}</td></tr>`,
    `<tr><td>Keywords: ${sessionInfo.keywords}<br/></td></tr></table>`,
  ].join('') : ''
));

const getTranscriptGEOInfo = memoize(1)(sessionInfo => (
  sessionInfo ? [
    '<table cellspacing="0" cellpadding="0"><tr><td>&nbsp;</td></tr><tr><td style="color:#329fd9"><b>GEO Info</b></td></tr>',
    `<tr><td>IP: ${sessionInfo.ip}</p></td></tr>`,
    `<tr><td>Country/Region: ${sessionInfo.country}</p></td></tr>`,
    `<tr><td>State/Province: ${sessionInfo.state}</p></td></tr>`,
    `<tr><td>City: ${sessionInfo.city}<br/></p></td></tr></table>`,
  ].join('') : ''
));

const getTranscriptClientInfo = memoize(1)(sessionInfo => (
  sessionInfo ? [
    '<table cellspacing="0" cellpadding="0"><tr><td>&nbsp;</td></tr><tr><td style="color:#329fd9"><b>Client Info</b></td></tr>',
    `<tr><td>Operating System: ${sessionInfo.operatingSystem}</td></tr>`,
    `<tr><td>Browser: ${sessionInfo.browser}</td></tr>`,
    `<tr><td>Flash Version: ${sessionInfo.flashVersion}</td></tr>`,
    `<tr><td>Screen Resolution: ${sessionInfo.screenResolution}</td></tr>`,
    `<tr><td>Language: ${sessionInfo.language}</td></tr>`,
    `<tr><td>Time Zone: ${sessionInfo.timeZone}<br/></td></tr></table>`,
  ].join('') : ''
));

const getTranscriptRating = memoize(1)(chatInfo => (
  chatInfo ? [
    '<table cellspacing="0" cellpadding="0"><tr><td>&nbsp;</td></tr><tr><td style="color:#329fd9"><b>Rating</b></td></tr>',
    `<tr><td>Rating: ${chatInfo.rating}</td></tr>`,
    `<tr><td>Rating Comment: ${chatInfo.ratingComment}<br/></td></tr></table>`,
  ].join('') : ''
));

const getTranscriptAgentComment = memoize(1)(chatInfo => (
  chatInfo ? [
    '<table cellspacing="0" cellpadding="0"><tr><td>&nbsp;</td></tr><tr><td style="color:#329fd9"><b>Agent Comment</b></td></tr>',
    `<tr><td>${chatInfo.agentComment}</td></tr></table>`,
  ].join('') : ''
));

/**
 * @todo
 * This is a very ugly implementation of cache pure function result.
 *
 * The main idea is: if input is the same, do not call genStaticSelect again to create a new
 * component.
 *
 * The code need to be improved later.
 */
let cachedDepartment = [];

const getOnlineDepartmentComponent = (state) => {
  const chatId = selectors.getSelectedChatId(state);
  const visitor = selectors.getVisitorByChatId(state, chatId); // TODO
  const departments = selectors.getDepartments(state).map(
    ({ id, name }) => {
      const availableAgentNumberByDepartmentId =
        selectors.getAvailableAgentsNumberByDepartmentId(state, id, visitor);
      return {
        /** @todo notice that following two lines are not actually aligned, could be an issue */
        text: `${name} (Online: ${availableAgentNumberByDepartmentId.onlineAgentNumber},
                          Away: ${availableAgentNumberByDepartmentId.awayAgentNumber})`,
        value: id,
      };
    }
  );
  const onlineAgentNumber = selectors.getOnlineAvailableAgents(state, visitor).length;
  const awayAgentNumber = selectors.getAwayAvailableAgents(state, visitor).length;
  const defaultSelectOption = [
    {
      text: `All Available (Online: ${onlineAgentNumber}, Away: ${awayAgentNumber})`,
      value: -1,
    },
  ];
  const options = defaultSelectOption.concat(departments);
  if (options.length !== cachedDepartment.length) {
    cachedDepartment = options;
  } else {
    for (let i = 0; i < options.length; i += 1) {
      if (options[i].text !== cachedDepartment[i].text ||
        options[i].id !== cachedDepartment[i].id) {
        cachedDepartment = options;
        break;
      }
    }
  }
  return cachedDepartment;
};

const getChatActionDisplay = memoize(1)(
  (siteInfo, ifHaveTransferPermission, ifEnableTranslate, ifG2MConnected,
    campaign, ifSiteEnableAudioVideoChat) => ({
      transfer: siteInfo.ifEnableTeamWork && ifHaveTransferPermission,
      /** @todo following is not used in <SendFile />, could be an issue */
      sendFile: siteInfo.ifCanUseAdvancedChatFunc,
      sendTranscript: siteInfo.ifCanEmailTranscript,
      attachTicket: siteInfo.ifTicketOpen,
      autoTranslate: siteInfo.ifEnableChatTranslation && siteInfo.ifEnableChatIntegration &&
      ifEnableTranslate,
      gotoMeeting: siteInfo.ifEnableScreenSharing && siteInfo.ifEnableGotoMeeting &&
      ifG2MConnected,
      joinMe: siteInfo.ifEnableJoinMeScreenSharing,
      pciForm: siteInfo.ifEnablePCIForm,
      audioChat: ifSiteEnableAudioVideoChat && helper.ifSupportWebrtc && campaign
      && campaign.ifEnableAudioChat,
      videoChat: ifSiteEnableAudioVideoChat
      && helper.ifSupportWebrtc && campaign && campaign.ifEnableVideoChat,
    }));

const getChatActionEnable = memoize(1)(
(visitor, chatExists, currentAgentId, ifUseMainServer, ifEnableAudioVideoChat) => {
  const isSendingMessageAllowed = helper.isSendingMessageAllowed(visitor, currentAgentId);
  const status = visitor.get('status');
  const ifManuallyInvitedAndWaiting =
    status === visitorStatus.manuallyInvitedAndWaitingVisitor;
  const isActuallyChatting = isSendingMessageAllowed && !ifManuallyInvitedAndWaiting;
  return {
    transfer: isActuallyChatting,
    sendFile: isActuallyChatting,
    sendTranscript: status === visitorStatus.chatEnded,
    attachTicket: helper.isAttachTicketAllowed(visitor, chatExists, currentAgentId),
    autoTranslate: isActuallyChatting,
    gotoMeeting: isActuallyChatting,
    joinMe: isActuallyChatting && ifUseMainServer,
    pciForm: isSendingMessageAllowed,
    audioChat: isActuallyChatting && ifEnableAudioVideoChat, // agent 是否在audio/video chatting
    videoChat: isActuallyChatting && ifEnableAudioVideoChat, // agent 是否在audio/video chatting
  };
});

const getTranscriptInfo = memoize(1)(
  (visitor, chatId, chatMessages, currentAgentName, currentAgentEmail,
    sessionInfo, chatInfo, dateTimeFormat, ifDepartmentEnabled) => {
    const transcriptVisitorInfo = getTranscriptVisitorInfo(visitor, dateTimeFormat);

    const transcriptNavigation = getTranscriptNavigation(sessionInfo, dateTimeFormat);
    const transcriptGEOInfo = getTranscriptGEOInfo(sessionInfo);
    const transcriptClientInfo = getTranscriptClientInfo(sessionInfo);
    const transcriptRating = getTranscriptRating(chatInfo);
    const transcriptAgentComment = getTranscriptAgentComment(chatInfo);
    const transcriptBody = getInitialTranscriptBody(
      visitor,
      chatId,
      chatMessages,
      chatInfo,
      dateTimeFormat,
      ifDepartmentEnabled
    );
    return {
      chatId,
      transcriptVisitorInfo,
      transcriptNavigation,
      transcriptGEOInfo,
      transcriptClientInfo,
      transcriptRating,
      transcriptAgentComment,
      to: `${currentAgentName} <${currentAgentEmail}>`,
      subject: `Chat_${chatId}`,
      body: transcriptBody,
    };
  });

const getAutoTranslateInfo = memoize(1)(
  (detectedVisitorLanguage, ifAutoPopTranslation, ifEnable, agentTranslationLanguage) => {
    const ifOpenAutoTranslation = !ifAutoPopTranslation && !ifEnable &&
      detectedVisitorLanguage !== '0' &&/** @todo what does '0' stands for? */
      detectedVisitorLanguage !== agentTranslationLanguage;
    return {
      detectedVisitorLanguage,
      ifAutoPopTranslation,
      ifOpenAutoTranslation,
    };
  });

const getPCIForm = memoize(1)(
  (secureFormServer, secureFormConfig, pciFormList) => ({
    secureFormServer,
    secureFormConfig,
    pciFormList,
  })
);

const getTransfer = memoize(1)(
  (ifDepartmentEnabled, onlineDepartments, onlineDepartmentComponent, allAvailableAgents) => ({
    ifDepartmentEnabled,
    onlineDepartments,
    onlineDepartmentComponent,
    allAvailableAgents,
  })
);

function ifEnableAudioVideoChatButton(state, chat, currentAgentId) {
  if (!chat.get('ifVisitorSupportWebrtc')) {
    return false;
  }

  const visitor = selectors.getVisitor(state, chat.get('visitorId'));
  const avStatus = visitor ? visitor.get('audioVideoChatStatus') : audioVideoChatStatus.notStart;
  if (avStatus !== audioVideoChatStatus.notStart) {
    return false;
  }

  if (selectors.getIfAgentAudioVideoChatting(state, currentAgentId)) {
    return false;
  }

  if (selectors.getIfAgentRequestAudioVideoChat(state, currentAgentId)) {
    return false;
  }

  return true;
}

const mapStateToProps = (state) => {
  const chatId = selectors.getSelectedChatId(state);
  if (!chatId) {
    return {};
  }
  perf.markBegin('ChatAction: mapStateToProps');
  const siteId = selectors.getSiteId(state);
  const visitor = selectors.getVisitorByChatId(state, chatId);
  const chatExists = selectors.ifChatExists(state, chatId);
  const visitorId = visitor.get('id');
  const ifDepartmentEnabled = selectors.getifDepartmentEnabled(state);
  const currentAgentId = selectors.getCurrentAgentId(state);
  const onlineDepartments = selectors.getTransferOnlineDepartments(state, visitor);
  const allAvailableAgents = selectors.getAllAvailableAgents(state, visitor);
  const currentAgentName = selectors.getCurrentAgentName(state);
  const currentAgentEmail = selectors.getCurrentAgentEmail(state);
  const agentSession = selectors.getCurrentAgentSession(state);
  const joinMeIntegrationServer = selectors.getJoinMeIntegrationServer(state);
  const chat = selectors.getSelectedChat(state);
  const secureFormServer = selectors.getSecureFormServer(state);
  const campaign = selectors.getCodePlan(state, visitor.get('codePlanId'));
  const ifUseMainServer = selectors.ifUseMainServer(state);
  const chatDetails = selectors.getChatDetails(state, visitorId, chatId);
  const dateTimeFormat = selectors.getDateTimeFormat(state);
  const siteInfo = selectors.getSiteInfo(state);
  const ifHaveTransferPermission = selectors.getPermission(state, permissionType.transferChat);

  const chatInfo = _.get(chatDetails, 'chatInfo');
  const sessionInfo = _.get(chatDetails, 'sessionInfo');

  const onlineDepartmentComponent = getOnlineDepartmentComponent(state);

  const authProvider = ifUseMainServer ?
    selectors.getChatServerUrl(state).replace('https://', '').replace('http://', '')
    :
    selectors.getStandbyChatServer(state).replace('https://', '').replace('http://', '');
  const ifAutoPopTranslation = selectors.ifPopTranslation(state, chatId);
  const enableChatTranslation = selectors.getIfChatEnableTransaltion(state, chatId);

  const secureFormConfig = {
    siteId,
    visitorId,
    authToken: agentSession,
    authProvider,
    themeColor: campaign ? campaign.themeColor : '',
  };
  const detectedVisitorLanguage = selectors.getVisitorLanguage(state, chatId).toString();
  const agentTranslationLanguage =
    selectors.getAgentTranslationLanguage(state).toString();
  const fileUploadUrl = selectors.getFileUploadUrl(state);
  const sendFileUploadUrl = `${fileUploadUrl}?sessionId=${agentSession}&visitorId=${visitorId}&siteId=${siteId}`;

  const {
    isWeb,
    attachTicketUrl,
    loginUrl,
    autoLoginUrl,
  } = common.getAttachTicketURL(state, visitor.get('ticketId').split('-T')[1]);

  const responseErrorMessage = selectors.getResponseErrorMessage(state);
  const sendEmailSuccessMessage = selectors.getSendEmailSuccessMessage(state);

  const transcriptInfo = getTranscriptInfo(
    visitor, chatId, chat.get('messages'), currentAgentName, currentAgentEmail,
    sessionInfo, chatInfo, dateTimeFormat, ifDepartmentEnabled
  );

  const ret = ({
    responseErrorMessage,
    sendEmailSuccessMessage,
    chatActionDisplay: getChatActionDisplay(
      siteInfo,
      ifHaveTransferPermission,
      selectors.getIfEnableTranslate(state),
      selectors.getConfigIfG2MConnected(state),
      selectors.getCodePlan(state, visitor.get('codePlanId')),
      selectors.ifSiteEnableAudioVideoChat(state),
    ),
    chatActionEnable: getChatActionEnable(
      visitor,
      chatExists,
      currentAgentId,
      selectors.getIfUseMainServer(state),
      ifEnableAudioVideoChatButton(state, chat, currentAgentId),
    ),
    siteId,
    agentSession,
    ifUseMainServer,
    selectedFontSize: selectors.getFontSize(state),
    siteInfo: {
      visitorId,
      uploadUrl: selectors.getFileUploadUrl(state),
      downloadUrl: selectors.getFileDownloadUrl(state),
      sessionId: selectors.getCurrentAgentSession(state),
      siteId,
      privateMessage: 1,
      maxFileSize: selectors.getMaxFileSize(state),
      maxFileNum: selectors.getMaxFileNum(state),
      sendFileUploadUrl,
    },
    visitorId,
    chatId,
    files: selectors.getUploadedFiles(state, chatId),
    ticketId: visitor.get('ticketId'),
    joinMeIntegrationServer,
    currentAgentId,
    transfer: getTransfer(
      ifDepartmentEnabled,
      onlineDepartments,
      onlineDepartmentComponent,
      allAvailableAgents,
    ),
    transcriptInfo,
    pciForm: getPCIForm(secureFormServer, secureFormConfig, selectors.getPCIForms(state)),
    autoTranslateInfo: getAutoTranslateInfo(
      detectedVisitorLanguage,
      ifAutoPopTranslation,
      enableChatTranslation,
      agentTranslationLanguage
    ),
    gotoAttachTicket() {
      common.gotoAttachTicket({
        isWeb,
        attachTicketUrl,
        loginUrl,
        autoLoginUrl,
      });
    },
    chatGuid: visitor.get('chatGuid'),
    webrtcSignalingServiceUrl: getWebrtcSignalingServiceUrl(state),
  });
  perf.markEnd('ChatAction: mapStateToProps');
  return ret;
};

const mapDispatchToProps = dispatch => ({
  onSendFileStart(chatId, filename, guid) {
    dispatch(actions.startUpload(chatId, filename, guid));
  },
  onSendFileProgress(chatId, percentage, filename, guid) {
    dispatch(actions.uploadFile(chatId, percentage, filename, guid));
  },
  onSendFileSuccess(chatId, filename, guid) {
    dispatch(actions.uploadFile(chatId, 100, filename, guid));
  },
  onSendFileCancel(chatId, filename, guid) {
    dispatch(actions.cancelUpload(chatId, filename, guid));
  },
  onFontSelect(selectedFontSize) {
    dispatch(uiActions.changeFontSize(selectedFontSize));
    dispatch(webAction.setPreference());
  },

  handleAttachTicket(visitorId, ticketId) {
    if (ticketId === 0) {
      dispatch(webAction.attachNewTicket(visitorId));
    } else {
      dispatch(webAction.attachToExistTicket(visitorId, ticketId));
    }
  },
  onRequestGotoMeeting(visitorId) {
    dispatch(webAction.createG2MMeeting(visitorId));
  },
  onTransferIconClick() {
    dispatch(webAction.getAgents());
  },
  onTransferChat(transferType, visitorId, departmentId, agentId, ifCloseChat) {
    if (transferType === '0' && departmentId > 0) {
      dispatch(agentAction.transferChatToDepartment(visitorId, departmentId, ifCloseChat));
    } else if (transferType === '1' && agentId > 0) {
      dispatch(agentAction.transferChatToAgent(visitorId, agentId, ifCloseChat)); // todo
    }
  },
  onToggleAutoTranslate(turnOnOrTurnOff, visitorId, language) {
    if (turnOnOrTurnOff === turnOnAutoTranslate) {
      dispatch(agentAction.enableTranslation(visitorId, language));
    } else {
      dispatch(agentAction.disableTranlsation(visitorId));
    }
  },
  handleSendTranscript(chatId, form) {
    if (process.env.NODE_ENV !== 'production') {
      console.log(form);
    }
    dispatch(webAction.sendEmail(chatId, form));
  },
  showResponseErrorMessage(errorMsg) {
    dispatch(uiActions.setResponseErrorMessage(errorMsg));
  },
  hideResponseErrorMessage() {
    dispatch(uiActions.setResponseErrorMessage(''));
  },
  hideSendEmailSuccessMessage() {
    dispatch(uiActions.setSendEmailSuccessMessage(''));
  },
  onUpdateIfPopTranslation(chatId) {
    dispatch(actions.onUpdateIfPopTranslation(chatId));
  },
  initialSendTranscriptForm(visitorId, chatId, transcriptInfo) {
    /** @todo following code could be improved */
    dispatch(webAction.getChatHistoryDetails(visitorId, chatId));
    dispatch(actions.initSendTranscriptForm({
      to: transcriptInfo.to,
      subject: transcriptInfo.subject,
      body: transcriptInfo.body.content,
    }));
  },
  onRequestPCIForm(serverPath, secureForm, formConfig, visitorId, chatId, pciFormName) {
    requestSecureForm(serverPath, secureForm, formConfig)
      .then(({ token, rsaKey }) => {
        dispatch(webAction.requestSecureForm(visitorId, pciFormName, token));
        dispatch(actions.onRequestedSecureForm(chatId, token, rsaKey));
      })
      .catch((error) => {
        dispatch(actions.setErrorMessageForChat(chatId, `Request PCI Form failed: ${error.message}`));
      });
  },
  showErrorMessage(chatId, errorMsg) {
    dispatch(actions.setErrorMessageForChat(chatId, errorMsg));
  },
  onRequestAudioChat({ webrtcSignalingServiceUrl, chatGuid, chatId, visitorId }) {
    const p2pChat = new P2PChat({
      url: webrtcSignalingServiceUrl,
      chat: chatGuid,
      localVideo: document.createElement('VIDEO'),
      remoteVideo: document.createElement('VIDEO'),
    });
    p2pChat.requirePermission(false).then(() => {
      dispatch(actions.setP2PChat(chatId, p2pChat));
      dispatch(webAction.onRequestAudioChat(visitorId));
    });
  },
  onRequestVideoChat({ webrtcSignalingServiceUrl, chatGuid, chatId, visitorId }) {
    const p2pChat = new P2PChat({
      url: webrtcSignalingServiceUrl,
      chat: chatGuid,
      localVideo: document.createElement('VIDEO'),
      remoteVideo: document.createElement('VIDEO'),
    });
    p2pChat.requirePermission(true).then(() => {
      dispatch(actions.setP2PChat(chatId, p2pChat));
      dispatch(webAction.onRequestVideoChat(visitorId));
    });
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(ChatAction);



