import React, { PropTypes } from 'react';
import ReactTooltip from 'react-tooltip';
import commonStyle from '../CommonResources/common.css';
import styles from './ChatItem.css';
import ChatItemConfig from './ChatItemConfig';
import ShowChatViaGroup from './ShowChatViaGroup';
import ShowChatViaList from './ShowChatViaList';
import { icons } from '../../constants/enums';
import Icon from '../Icon/Icon';

const ChatItemContainer = props => (
  <div className={styles.itemContainer}>
    {
      props.isShowChatItemsViaStatus ?
        (
          <ShowChatViaGroup
            groupItems={props.groupItems}
            enableRefuseChat={props.enableRefuseChat}
            ifEnableSegmentation={props.ifEnableSegmentation}
            isShowUnreposiveTime={props.isShowUnreposiveTime}
            isShowDepartment={props.isShowDepartment}
            onClickPin={props.onClickPin}
            onItemClick={props.onItemClick}
            onAcceptChat={props.onAcceptChat}
            onRefusedChat={props.onRefusedChat}
            onWrapUp={props.onWrapUp}
            onDeleteItem={props.onDeleteItem}
            onClickArrow={props.onClickArrow}
          />
        ) : (
          <ShowChatViaList
            chats={props.chats}
            enableRefuseChat={props.enableRefuseChat}
            ifEnableSegmentation={props.ifEnableSegmentation}
            isShowUnreposiveTime={props.isShowUnreposiveTime}
            isShowDepartment={props.isShowDepartment}
            onClickPin={props.onClickPin}
            onItemClick={props.onItemClick}
            onAcceptChat={props.onAcceptChat}
            onRefusedChat={props.onRefusedChat}
            onWrapUp={props.onWrapUp}
            onDeleteItem={props.onDeleteItem}
          />
        )
    }
    <div className={styles.configPanel}>
      <div>
        <Icon
          type={icons.chatListSetting}
          size={14}
          tabIndex={0}
          className={styles.configicon}
        >
          <ChatItemConfig
            isShowChatItemsViaStatus={props.isShowChatItemsViaStatus}
            isShowUnreposiveTime={props.isShowUnreposiveTime}
            ifDepartmentEnabled={props.ifDepartmentEnabled}
            isShowDepartment={props.isShowDepartment}
            onClickConfig={props.onClickConfig}
          />
        </Icon>
      </div>
    </div>
    <ReactTooltip
      id="chatlist"
      effect="solid"
      place="bottom"
      class={commonStyle.tooltipInfoNoCapitalize}
    />
  </div>
);

ChatItemContainer.propTypes = {
  chats: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
  groupItems: PropTypes.arrayOf(PropTypes.shape({
    description: PropTypes.string.isRequired,
    chats: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
    opened: PropTypes.bool,
  })).isRequired,
  /* following setting for all chat */
  isShowChatItemsViaStatus: PropTypes.bool.isRequired,
  isShowUnreposiveTime: PropTypes.bool.isRequired,
  ifDepartmentEnabled: PropTypes.bool.isRequired,
  isShowDepartment: PropTypes.bool.isRequired,
  ifEnableSegmentation: PropTypes.bool,
  enableRefuseChat: PropTypes.bool,
  onClickConfig: PropTypes.func.isRequired,
  onClickPin: PropTypes.func.isRequired,
  onItemClick: PropTypes.func.isRequired,
  onAcceptChat: PropTypes.func.isRequired,
  onRefusedChat: PropTypes.func.isRequired,
  onWrapUp: PropTypes.func.isRequired,
  onDeleteItem: PropTypes.func.isRequired,
  onClickArrow: PropTypes.func.isRequired,
};
ChatItemContainer.examples = {
  'Default Example': {
    description: 'chat container',
    component:
  <ChatItemContainer
    chatItems={[]}
  />,
  },
};
export default ChatItemContainer;



