import React from 'react';
import classnames from 'classnames';
import Button from 'src/components/Button/Button';
import chatActionStyle from '../../ChatSection/ChatAction/ChatAction.css';
import Icon from '../../Icon/Icon';
import { icons as enums } from '../../../constants/enums';

const VideoChat = props =>
  <div>
    <Button
      type="noborder"
      className={
        classnames(
          chatActionStyle.chatActionIcon,
          !props.ifEnable && chatActionStyle.chatActionIconDisabled
        )
      }
      onClick={props.ifEnable ? props.onClick : undefined}
      data-tip="Video Chat"
      data-for="chatContainer"
    >
      <Icon type={enums.videoChat} />
    </Button>
  </div>;

export default VideoChat;

VideoChat.propTypes = {
  ifEnable: React.PropTypes.bool,
  onClick: React.PropTypes.func,
};



