import React from 'react';
import Button from 'src/components/Button/Button';
import ModalStyle from '../Modal.css';
import Icon from '../../Icon/Icon';
import { icons } from '../../../constants/enums';

// class SelectDepartment extends React.Component {
const SelectDepartment = props => (
  props.onlineDepartments.length ?
    <div className={ModalStyle.agentContainer}>
      <ul className={ModalStyle.availableList}>
        {
          props.onlineDepartments.map((item, index) => (
            <li key={index}>
              <Button
                type="noborder"
                className={props.selectedDepartmentIndex === index ? ModalStyle.active : ''}
                onClick={() => props.handleSelectDepartment(item.id, index)}
              >
                <Icon
                  type={icons.statusOnline}
                  size={16}
                  color="#329fd9"
                />
                <div className={ModalStyle.transferName}>{item.name}</div>
              </Button>
            </li>
          ))
        }
      </ul>
    </div>
    :
    <p>No department is available at the moment.</p>
);

export default SelectDepartment;

SelectDepartment.propTypes = {
  onlineDepartments: React.PropTypes.arrayOf(React.PropTypes.shape({
    id: React.PropTypes.number,
    name: React.PropTypes.string,
  })),
  selectedDepartmentIndex: React.PropTypes.number,
  handleSelectDepartment: React.PropTypes.func,
};



